import React, { Component } from "react";
import { connect } from "react-redux";
import Launch from "./Launch";

import Preloader from "../ui/Preloader";

class LaunchesPage extends Component {
  renderLaunches = () => {
    return this.props.launches.map(launch =>
      <Launch key={launch.id} data={launch} />
    );
  };
  render() {
    return (
      <div>
        <div className="container">
          {this.renderLaunches()}
          {this.props.launches.length ? "" : <Preloader />}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ data: { launches } }) => ({
  launches
});

export default connect(mapStateToProps)(LaunchesPage);



