import React, { Component } from "react";
import { Link } from "react-router-dom";
import ClockO from "react-icons/lib/fa/clock-o";

import LabelValue from "../ui/LabelValue";
import Card from "../ui/Card";
import Button from "../ui/Button";

import format from "date-fns/format";

class Launch extends Component {
  mapMissions = mis => {
    return (
      <span className="launch-mission-list">
        {mis.length === 0 ? this.props.data.name : mis.map(m => m.name)}
      </span>
    );
  };

  mapDescription = mis => {
    return mis.map(m => m.description).join(" ");
  };

  mapLinks = vidURLs => {
    return vidURLs.map(url => {
      let title = "Info";
      if (url.indexOf("youtube") > -1) {
        title = "Youtube stream";
      }
      return (
        <a className="launch-link" href={url}>
          {title}
        </a>
      );
    });
  };
  render() {
    const launchDate = new Date(this.props.data.net);
    const { id, rocket, missions } = this.props.data;
    return (
      <Card>
        <LabelValue label="rocket" value={rocket.name} />
        <LabelValue label="mission" value={missions[0].name} medium />
        <LabelValue
          label={
            <div>
              <ClockO />when
            </div>
          }
          value={format(launchDate, "D MMMM HH:mm")}
          medium
        />
        <p>
          {this.mapDescription(missions)}
        </p>
        <div style={{ textAlign: "right" }}>
          <Link to={`/launch/${id}`}>
            <Button dummy>More</Button>
          </Link>
        </div>
      </Card>
    );
  }
}

export default Launch;



